import React, { useState, useEffect } from "react"
import { useParams } from "react-router-dom"
import Axios from "axios";

import Box from '@mui/material/Box';
import Fab from '@mui/material/Fab';
import AddIcon from '@mui/icons-material/Add';
import IconButton from '@mui/material/IconButton';
import EditIcon from '@mui/icons-material/Edit';
import Button from '@mui/material/Button';
import Alert from '@mui/material/Alert';
import { set } from "date-fns";
import { AddCard } from "@mui/icons-material";






export default function MemberPage(props) {
    const currentMember = props.currentMember //current user
    const { memberURL } = useParams();
    const [memberURLState, setMemberURLState] = useState();
    const [member, setMember] = useState(); //member that currently viewed profile belongs to
    const [aboutMeContent, setAboutMeContent] = useState();
    const [edit, setEdit] = useState();
    const [successAlert, setSuccessAlert] = useState(false);

    async function loadMemberData(memberURL) {
        const memberData = await Axios.get(process.env.REACT_APP_MAIN_DOMAIN + '/member/' + memberURL)
            .then(function (response) {
                setMember(response.data);
                console.log(response.data)
            })
    }

    useEffect(() => {
        console.log("useEfer")
        loadMemberData(memberURL)
    }, [memberURL])

    useEffect(()=>{
    if(member){
    setAboutMeContent(member.about_me)
    }
    },[member])

    function handleEdit(id){
    if(edit !== id){
    setEdit(id)
    }
    else{
    setEdit()
    }
    }

    async function handleSave(field, content){
        const toUpdate = {[field]: content}
        console.log(toUpdate)
       const updateMember = await Axios.post(process.env.REACT_APP_MAIN_DOMAIN + '/current-member/update', toUpdate, {withCredentials: true})
       .then(function (response) {
           if(response){
               setSuccessAlert(true);
               setTimeout(() => {
                   setSuccessAlert(false)
               }, 5000);
           }
           console.log(response.data)
       })
} 

    return (
        <div>
            {member ?
                currentMember ?
                    <div>
                        <h1>Member Profile</h1>
                        {successAlert && <Alert severity="success">Your updates have been saved!</Alert>}
                        <div className="member-page-wrapper">
                            <div className="member-page-sidebar">
                                Sidebar
                                <Box className="member-page-sidebar-element" sx={{ boxShadow: 2 }}>
                                    <img className="member-page-profile-pic" src={member.profile_pic}></img>
                                    <h3>{member.first_name} {member.last_name}</h3>
                                </Box>
                                <Box className="member-page-sidebar-element" sx={{ boxShadow: 2 }}>
                                    <h3>Languages</h3>
                                    <p className="member-page-sidebar-element-title">Native in:</p>
                                    <Box className="member-page-sidebar-element-sub" sx={{ boxShadow: 2 }}>
                                        {member.native_language}
                                    </Box>
                                    {(member.fluent_languages != "") && <div><p className="member-page-sidebar-element-title">Fluent in:</p>
                                        {member.fluent_languages.map((language, index) => (<Box className="member-page-sidebar-element-sub" key={index} sx={{ boxShadow: 2 }}>
                                            {language}
                                        </Box>))}
                                    </div>}
                                    <p className="member-page-sidebar-element-title">Learning:</p>
                                    {member.learning_languages.map((language, index) => (<Box className="member-page-sidebar-element-sub" key={index} sx={{ boxShadow: 2 }}>
                                        {language}
                                    </Box>))}
                                </Box>
                            </div>
                            <div className="member-page-main">
                                Main
                                <div>
                                    {(member._id === currentMember._id) && !aboutMeContent && <div>
                                    <Fab onClick={()=>{setAboutMeContent("Tell about yourself here (don't forget to save)")}}>
                                        <AddIcon fontSize="large" color="primary"></AddIcon>
                                    </Fab>
                                    Add "About me" card
                                    </div>}
                                    {aboutMeContent && <Box className = "member-page-card" sx={{ boxShadow: 2, minHeight: "50px", textAlign: "center" }}>
                                        <h3>About Me</h3>
                                        {(member._id === currentMember._id) && 
                                        <IconButton id="about-me-edit"  onClick={(e) =>{handleEdit(e.currentTarget.id)}} style={{position: "absolute", right: 0}}>
                                            <EditIcon></EditIcon>
                                        </IconButton>
                                        }
                                        {!(edit === "about-me-edit") && <p>{aboutMeContent}</p>}
                                        {(edit === "about-me-edit") && <div style={{display:"flex", flexDirection: "column"}}>
                                        <input type="text" style={{textAlign: "center", border: "none", fontSize: "16px", backgroundColor: "lightblue", padding: "5px", margin: "16px"}} onChange= {(e)=>{setAboutMeContent(e.target.value)}} value={aboutMeContent}></input>
                                        <Button sx={{margin: "auto"}} onClick={()=>{handleSave("about_me", aboutMeContent)}}>Save</Button>
                                        </div>}
                                    </Box>}
                                </div>
                            </div>
                        </div>
                    </div>
                    :
                    <h2>Finish setting up your member profile to view other members!</h2>
                :
                <h2>No member</h2>
            }
        </div>
    )
}