import React from "react"
import TextField from '@mui/material/TextField';
import Select from '@mui/material/Select';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Button from "@mui/material/Button";
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import DateTimePicker from '@mui/lab/DateTimePicker';
import Axios from "axios";
import { useNavigate } from 'react-router-dom';



function BookEvent(props) {

    const currentMember = props.currentMember;

    const [eventName, setEventName] = React.useState("");
    const [eventStartDate, setEventStartDate] = React.useState(new Date());


    let navigate = useNavigate();
    const routeChange = (path) => {
        navigate(path);
    }


    const handleSubmit = (event) => {
        event.preventDefault()
        // console.log(image)
        const newEvent = {
            eventName: eventName,
            eventStartDate: eventStartDate
        }
        return Axios.post(process.env.REACT_APP_MAIN_DOMAIN + '/book-event',
            newEvent,
            { withCredentials: true }
        )
            .then(function (response) {
                console.log(response)
                routeChange("/event/" + response.data.event_URL)

            })
    }

    return (
        <div>
            {currentMember ?
                <div className="book-event-wrapper">
                    <h1>Book an Event</h1>
                    <form className="book-event-form" onSubmit={handleSubmit}>

                        {/*-------------------------------- EVENT NAME ----------------------------------------*/}
                        <div className="book-event-form-entry">
                            <TextField
                                fullWidth
                                required
                                id="event-name"
                                label="Event Name"
                                placeholder="ex. Japanese Weekly Chat"
                                value={eventName}
                                onChange={(e) => { setEventName(e.target.value) }}
                            />
                        </div>
                        {/*-------------------------------- EVENT START DATE ----------------------------------------*/}
                        <div className="book-event-form-entry">
                            <LocalizationProvider dateAdapter={AdapterDateFns}>
                                <DateTimePicker
                                    renderInput={(props) => <TextField {...props} />}
                                    label="Event Date and Time"
                                    value={eventStartDate}
                                    onChange={(newValue) => {
                                        setEventStartDate(newValue);
                                        console.log(newValue)
                                    }}
                                />
                            </LocalizationProvider>
                        </div>
                        {/*-------------------------------- EVENT DURATION ----------------------------------------*/}
                        <p>Duration</p>
                        {/*-------------------------------- EVENT DESCRIPTION----------------------------------------*/}
                        <p>Description</p>
                        <p>Max attendees</p>
                        <p>Event Pic</p>
                        <p>Event Location</p>
                        <p>Event Platform</p>
                        <p>Meeting URL</p>
                        {/* <div className="create-group-form-entry">
                <FormControl fullWidth>
                    <InputLabel id="group-type-select-label">Group Type</InputLabel>
                    <Select
                        labelId="group-type-select-label"
                        required
                        id="group-type"
                        value={groupType}
                        label="Group Type"
                        onChange={handleChange}
                    >
                        <MenuItem value={"Language Exchange"}>Language Exchange</MenuItem>
                        <MenuItem value={"Study Group"}>Study Group</MenuItem>
                        <MenuItem value={"Class"}>Class</MenuItem>
                    </Select>
                </FormControl>
                </div> */}
                        <Button
                            variant="contained"
                            type="submit"
                        >Submit</Button>
                    </form>
                </div>
                : <h1>Open a member profile to book events!</h1>
            }
        </div>
    )
}

export default BookEvent