import React, { useEffect, useState } from "react"
import { useNavigate } from 'react-router-dom';
import TextField from '@mui/material/TextField';
import Select from '@mui/material/Select';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import ImageUpload from '../newCropper/ImageUpload';
import Alert from '@mui/material/Alert';
import PhotoCamera from '@mui/icons-material/PhotoCamera';
import Button from "@mui/material/Button";
import Box from '@mui/material/Box';
import OutlinedInput from '@mui/material/OutlinedInput';
import Chip from '@mui/material/Chip';
import FormHelperText from '@mui/material/FormHelperText';
import Axios from "axios";
import languageList from "./languages";



function CreateGroup(props) {
    const currentMember = props.currentMember;

    const [groupName, setGroupName] = React.useState("");
    const [groupType, setGroupType] = React.useState('');
    const [image, setImage] = React.useState('');
    const [groupPrivacy, setGroupPrivacy] = React.useState('');
    const [groupLanguages, setGroupLanguages] = React.useState([]);

    const [alert, setAlert] = React.useState(false);
    const [cropper, setCropper] = React.useState(false);

    const ITEM_HEIGHT = 48;
    const ITEM_PADDING_TOP = 8;
    const MenuProps = {
        PaperProps: {
            style: {
                maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
                width: 250,
            },
        },
    };


    const handleChangeType = (event) => {
        setGroupType(event.target.value);
    };

    const handleChangePrivacy = (event) => {
        setGroupPrivacy(event.target.value);
    };

    const handleChangeLanguages = (event) => {
        const {
            target: { value },
        } = event;
        setGroupLanguages(
            // On autofill we get a stringified value.
            typeof value === 'string' ? value.split(',') : value,
        );
    };

    let navigate = useNavigate();
    const routeChange = (path) => {
        navigate(path);
    }

    const handleSubmit = (event) => {
        event.preventDefault()
        // console.log(image)
        const group = {
            groupName: groupName,
            groupType: groupType,
            groupPic: image,
            groupPrivacy: groupPrivacy,
            groupLanguages: groupLanguages
        }
        return Axios.post(process.env.REACT_APP_MAIN_DOMAIN + '/create-group',
            group,
            { withCredentials: true }
        )
            .then(function (response) {
                console.log(response)
                routeChange("/group/" + response.data.group_URL)
            })
    }

    return (
        <div>
            {currentMember ?
                <div className="create-group-wrapper">
                    <h1>Create a Langroop</h1>
                    <form className="create-group-form" onSubmit={handleSubmit}>

                        {/* --------------------------------------GROUP NAME----------------------------------------- */}
                        <div className="create-group-form-entry">
                            <TextField
                                fullWidth
                                required
                                id="group-name"
                                label="Group Name"
                                placeholder="ex. Japanese Study Group"
                                value={groupName}
                                onChange={(e) => { setGroupName(e.target.value) }}
                            />
                        </div>

                        {/* --------------------------------------GROUP TYPE----------------------------------------- */}
                        <div className="create-group-form-entry">
                            <FormControl fullWidth>
                                <InputLabel id="group-type-select-label">Group Type</InputLabel>
                                <Select
                                    labelId="group-type-select-label"
                                    required
                                    id="group-type"
                                    value={groupType}
                                    label="Group Type"
                                    onChange={handleChangeType}
                                >
                                    <MenuItem value={"Language Exchange"}>Language Exchange</MenuItem>
                                    <MenuItem value={"Study Group"}>Study Group</MenuItem>
                                    {/* <MenuItem value={"Class"}>Class</MenuItem> */}
                                </Select>
                            </FormControl>
                        </div>

                        {/* --------------------------------------GROUP PRIVACY----------------------------------------- */}
                        <div className="create-group-form-entry">
                            <FormControl fullWidth>
                                <InputLabel id="group-privacy-select-label">Group Privacy</InputLabel>
                                <Select
                                    labelId="group-privacy-select-label"
                                    required
                                    id="group-privacy"
                                    value={groupPrivacy}
                                    label="Group Privacy"
                                    onChange={handleChangePrivacy}
                                >
                                    <MenuItem value={"Community"}>Community</MenuItem>
                                    <MenuItem value={"Club"}>Club</MenuItem>
                                    <MenuItem value={"Class"}>Class</MenuItem>
                                </Select>
                            </FormControl>
                        </div>

                        {/*---------------------- GROUP LANGUAGES---------------------------- */}
                        <div>
                            <FormControl sx={{ m: 1, width: 300 }} required>
                                <InputLabel id="group-languages-selector-label">Group Languages</InputLabel>
                                <Select
                                    labelId="group-languages-selector-label"
                                    id="group-languages-selector"
                                    multiple
                                    name="groupLanguages"
                                    value={groupLanguages}
                                    onChange={handleChangeLanguages}
                                    input={<OutlinedInput id="group-languages-selector" label="Group Languages" />}
                                    renderValue={(selectedLanguages) => (
                                        <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                                            {selectedLanguages.map((value) => (
                                                <Chip key={value} label={value} />
                                            ))}
                                        </Box>
                                    )}
                                    MenuProps={MenuProps}
                                >
                                    {languageList.map((language) => (
                                        <MenuItem
                                            key={language.value}
                                            value={language.value}
                                        //   style={getStyles(language.label, language, theme)}
                                        >
                                            {language.label}
                                        </MenuItem>
                                    ))}
                                </Select>
                                <FormHelperText>(what languages will your group focus on?)</FormHelperText>
                            </FormControl>
                        </div>

                        {/* --------------------------------------GROUP PIC----------------------------------------- */}
                        <div className="group-pic-section">
                            <Button className="group-pic-display-button" component="span" onClick={() => { setCropper(true); setAlert(false) }}>
                                <PhotoCamera />
                            </Button>
                            <p>select a group cover picture</p>
                            <img className="group-pic-display" src={image} ></img>
                            {alert && <Alert severity="error">Please upload a group picture.</Alert>}
                        </div>

                        <div>
                            <Button
                                variant="contained"
                                type="submit"
                            >Submit</Button>
                        </div>
                    </form>
                    {cropper && <ImageUpload
                        setImage={setImage}
                        setCropper={setCropper}
                        width="640"
                        height="360"
                        cropShape="rectangle"
                    />}
                </div>
                :
                <h1>Open a member profile to create a group!</h1>
            }
        </div>
    )
}

export default CreateGroup
